Home = __decorate([
    Component({
        components: {
            banner,
            payBox,
            selectTab,
            imageUpload
        },
        filters: {
            getFormatDate(date) {
                return new Date(date).getFullYear() + '年' + (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '号';
            },
            getFoodNum(food) {
                return (food.num && food.num > 0) ? food.num : 0;
            }
        }
    })
], Home);
export default Home;
